import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home.vue'
import Task from '@/pages/Task.vue'
import ShortLink from '@/pages/ShortLink'



Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home
        },
        {
            path: '/task',
            name: 'Task',
            component: Task
        },
        {
            path: '/shortlink',
            name: 'ShortLink',
            component: ShortLink
        }
    ]
})