<template>
  <div class="main-task-todo">
    <div class="drag-preview" :style="dragStyle" ref="dragPreview"></div>
    <draggable class="list-group" :list="listTodos" group="tasks">
      <div
        @dragstart="dragStartHandler"
        @dragend="dragEndHandler"
        @drag="dragHandler"
        :key="listTodo.id"
        v-for="listTodo in listTodos"
        class="box-todo"
      >
        <div class="header-box-todo">
          <div class="header-title">
            <span
              :class="[
                listTodo.status ? 'dot-' + listTodo.status : '',
                'las la-braille header-dot',
              ]"
            ></span>
            <span class="title">{{ listTodo.title }}</span>
          </div>
          <span class="las la-thumbtack"></span>
        </div>
        <div class="main-box-todo">
          <p>
            {{ listTodo.description }}
          </p>
        </div>
      </div>
    </draggable>
  </div>
</template>

<script>
import draggable from "vuedraggable";
export default {
  name: "Todo",
  props: {
    todos: Object,
  },
  components: {
    draggable,
  },
  computed: {
    dragStyle() {
      return {
        top: `${this.y}px`,
        left: `${this.x}px`,
      };
    },
  },
  data() {
    return {
      listTodos: [],
      dragPreview: null,
      x: 0,
      y: 0,
    };
  },
  created() {
    this.listTodos = [
      {
        id: 1,
        title: "What is Lorem Ipsum?",
        description: "It is a long established fact",
        status: 1,
        idTaskTodo: 1,
      },
      {
        id: 2,
        title: "Why do we use it?",
        description: "It is a long established fact that a reader will",
        status: 2,
        idTaskTodo: 1,
      },
      {
        id: 3,
        title: "Where does it come from?",
        description:
          "It is a long established fact that a reader will be distracted by",
        status: 3,
        idTaskTodo: 2,
      },
      {
        id: 4,
        title: "Where can I get some?",
        description:
          "It is a long established fact that a reader will be distracted by the readable",
        status: 4,
        idTaskTodo: 3,
      },
      {
        id: 5,
        title: "What is Lorem Ipsum?",
        description:
          "It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. ",
        status: 5,
        idTaskTodo: 4,
      },
    ];
  },
  mounted() {
    this.getListTodo();
  },
  methods: {
    getListTodo() {
      console.warn("this.props.todos.id: ", this.todos.id);
      this.listTodos = this.listTodos.filter(
        (todos) => todos.idTaskTodo == this.todos.id
      );
    },
    dragStartHandler(e) {
      this.dragPreview = e.target.cloneNode(true);
      this.$refs["dragPreview"].appendChild(this.dragPreview);
      e.dataTransfer.setDragImage(new Image(), 0, 0);
    },

    dragEndHandler() {
      this.dragPreview.remove();
      this.dragPreview = null;
    },
    dragHandler(e) {
      this.x = e.pageX;
      this.y = e.pageY;
    },
  },
};
</script>

<style scoped>
.sortable-ghost {
  border: 1px dashed grey;
  font-size: 0;
  overflow: hidden;
}
.drag-preview {
  position: absolute;
  transform: translateX(-50%) translateY(-50%) rotate(7deg) translateY(55%);
  z-index: 1001;
  max-width: 250px;
}
.box-todo {
  padding: 1rem;
  background-color: white;
  border-radius: 15px;
  margin-bottom: 1rem;
}
.header-box-todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.header-box-todo .title {
  font-weight: bold;
}
.header-box-todo .las {
  font-size: 1.5rem;
  font-weight: bold;
}
.header-title {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header-title > span.header-dot {
  font-size: 1.5rem;
  margin-right: 5px;
}

.main-box-todo p {
  color: #929292;
  font-size: 0.8rem;
}
.dot-1 {
  color: #2c9985;
}
.dot-2 {
  color: #ff9400;
}
</style>